.box {
    width: 100%;
    min-height: 200px;
    margin-top: 90px;
    position: relative;
    display: flex;

    .wrapper {
        width: 1060px;
        margin: 0 auto;
        display: flex;
        flex-direction: column;
        position: relative;

        h3 {
            margin-top: 10px;
            color: var(--green, #056461);
            font-family: Montserrat;
            font-size: 36px;
            font-style: normal;
            font-weight: 600;
            line-height: normal;
        }

        .svg {
            position: absolute;
            top: 0;
            right: -180px;
            z-index: -1;
        }
    }


}

@media screen and (max-width: 1060px) {
    .box {
        .wrapper {
            width: 100%;
            margin: 0 10px 0 10px;
        }
    }
}

@media screen and (max-width: 900px) {
    .box {
        .wrapper {
            .svg {
                right: 50%;
                transform: translate(50%, -50px);
            }
        }
    }
}

@media screen and (max-width: 600px) {
    .box {

        .svg {

            right: calc(100vw / 3);
            transform: translate(50%);
        }
    }
}